import requests from '@/utils/axiosRequest'

export default {
    addOrigin(title,project){
        return requests({
            url:'/designs',
            method: 'POST',
            data: {
                title,project
            },
        })
    },
    getAllDesign(project){
        return requests({
            url:`/designs`,
            method: 'GET',
            params:{
                project
            }
        })
    },
    getSingleDesign(project,design){
        return requests({
            url:`/design/${design}`,
            method: 'GET',
            params: {
                project
            }
        })
    },
    changeOriginName(design){
        return requests({
            url:'/design',
            method: 'PATCH',
            data: {
                design
            }
        })
    },
    deleteOrigin(design){
        return requests({
            url:'/design',
            method: 'DELETE',
            data: {
                design
            }
        })
    },
    saveOrigin(content, style, design) {
        return requests({
            url: '/design-versions',
            method: 'POST',
            data: {
                content, style, design
            },
        })
    },
    getPassed(design) {
        return requests({
            url: `/design-versions/${design}`,
            method: 'GET',
            data: {
                design,
            },
        })
    },

}
